<template>
  <div class="container">
    <h1>대기질 정보</h1>
    <div class="input-group">
      <select v-model="stationName" @change="fetchAirQuality">
        <option value="">측정소 선택</option>
        <option value="주안">주안</option>
        <option value="종로구">종로구</option>
        <!-- 다른 측정소도 필요에 따라 추가할 수 있습니다 -->
      </select>
    </div>
    <div class="chart-container">
      <canvas ref="coChart" width="400" height="200"></canvas>
    </div>
  </div>
</template>

<script>
import Chart from 'chart.js/auto';
import axios from 'axios';

export default {
  data() {
    return {
      airQualityData: {},
      stationName: '',
      coChart: null
    };
  },
  methods: {
    fetchAirQuality() {
      if (this.stationName === '') {
        return; // 선택된 측정소가 없으면 함수 종료
      }
      
      const apiUrl = `https://apis.data.go.kr/B552584/ArpltnInforInqireSvc/getMsrstnAcctoRltmMesureDnsty?serviceKey=UG%2FoFdnta3Lg9FqBzA2vT8JPVkuPIX99gvnSZvXQ8aJnMRwzbLT6or1kf5l6bOSOSX2CgVF6MrBQqX7cgpybDA%3D%3D&returnType=xml&numOfRows=100&pageNo=1&stationName=${this.stationName}&dataTerm=DAILY&ver=1.0`;

      axios.get(apiUrl)
        .then(response => {
          const parser = new DOMParser();
          const xmlDoc = parser.parseFromString(response.data, 'text/xml');
          const items = xmlDoc.getElementsByTagName('item');
          
          const data = {};

          for (let i = 0; i < items.length; i++) {
            const item = items[i];
            const dataTime = item.getElementsByTagName('dataTime')[0].textContent;
            const measurements = {};

            const itemChildNodes = item.childNodes;
            for (let j = 0; j < itemChildNodes.length; j++) {
              const childNode = itemChildNodes[j];
              if (childNode.nodeType === 1 && childNode.nodeName !== 'dataTime') {
                measurements[childNode.nodeName] = childNode.textContent;
              }
            }

            data[dataTime] = measurements;
          }

          if (Object.keys(data).length === 0) {
            console.error('데이터를 받아오지 못했거나 유효하지 않습니다.');
            return;
          }

          this.airQualityData = data;

          const coValues = [];
          const timeLabels = [];

          Object.keys(this.airQualityData).forEach(time => {
            timeLabels.push(time);
            coValues.push(parseFloat(this.airQualityData[time]['coValue']));
          });

          this.destroyChart();
          this.createChart(timeLabels, coValues);
        })
        .catch(error => {
          console.error('API 요청 중 오류가 발생했습니다:', error);
        });
    },
    createChart(labels, coValues) {
      const ctx = this.$refs.coChart.getContext('2d');
      this.coChart = new Chart(ctx, {
        type: 'line',
        data: {
          labels: labels,
          datasets: [{
            label: 'CO 값',
            data: coValues,
            backgroundColor: 'rgba(54, 162, 235, 0.2)',
            borderColor: 'rgba(54, 162, 235, 1)',
            borderWidth: 1
          }]
        },
        options: {
          scales: {
            y: {
              min: 0,
              max: 1.0,
              ticks: {
                stepSize: 0.1
              }
            }
          }
        }
      });
    },
    destroyChart() {
      if (this.coChart) {
        this.coChart.destroy();
        this.coChart = null;
      }
    }
  }
};
</script>

<style>
.container {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
  font-family: Arial, sans-serif;
}

.input-group {
  display: flex;
  margin-bottom: 20px;
}

.input-group select {
  flex: 1;
  padding: 8px;
  font-size: 16px;
}

.chart-container {
  border: 1px solid #ddd;
  padding: 10px;
}
</style>
